* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

input {
  margin: 40px;
}
li{
  list-style: none;
  margin-bottom: 15px;
}

 div{
  margin-left: 40px;
}

.js-property-name{
  font-weight: bold;
}

.js-country-name{
  font-weight: bold;
  font-size: 35px;
}

span{
  margin-left: 5px;
}